}






/* -------------------------------MEDIA QUERIES (SMALL DEVICES)-------------------------- */
@media  screen and (max-width:600px) {
   .permallinks{
    flex-direction: column;
    gap: 1.5rem;
   }
   .footer_socials{
    margin-bottom: 2.6rem;
   }
}